.App {
	margin: auto;
	text-align: center;
	max-width: 1240px;

	height: 100vh;
}
.main {
	margin: auto;
	max-width: 100%;
	display: grid;
	grid-template-columns: repeat(2, 600px);
	column-gap: 40px;
}

@media screen and (max-width: 1200px) {
	.main {
		grid-template-columns: repeat(2, 1fr);
		column-gap: 10px;
	}
}

@media screen and (max-width: 780px) {
	.main {
		grid-template-columns: repeat(1, minmax(360px, 1fr));
	}
}
